import axios from "axios";
import { KEY } from "./constants";

const BASE_URL = 'https://api.themoviedb.org/3/';
const TRENDING_LIST = 'trending/movie/week';
const GENRES_LIST = 'genre/movie/list';

const configDefault = {
    params: {
        api_key: KEY,
        page: 1,
        language: 'en-US',
    }
};

export default class ServerRequest { //      ,   
    BASE_URL = BASE_URL; //  

    constructor(details = TRENDING_LIST, config = configDefault) {
        this.details = details; //     
        this.config = config; //      
    }

    get page() {
        return this.config.params.page; //    ,   
    }

    set page(newPage) {
        this.config.params.page = newPage; //      ,   
    }

    incrementPage() {
        this.config.params.page += 1; //     1,   
    }

    decrementPage() {
        this.config.params.page -= 1; //     1,   
    }

    reset() {
        this.config.params.query = null;
        this.config.params.page = 1; //       
    }

    get query() {
        return this.config.params.query;
    }

    set query(newQuery) {
        this.config.params.query = newQuery;
    }

    async getMovies() { //
        const url = this.BASE_URL + this.details; //         
        const response = await axios.get(url, this.config); //   

        return response.data; //    
    }

    async getGenres() {
        const url = this.BASE_URL + GENRES_LIST; //         
        const response = await axios.get(url, this.config); //     

        return response.data.genres; //    
    }
}
